let MauBinhUtils = {};
let Language     = require('maubinhLanguage');
MauBinhUtils.TEXT_BINH_TYPE = [
    Language.getString("binh_lung"),
    Language.getString("mau_thau"),
    Language.getString("mot_doi"),
    Language.getString("hai_doi"),
    Language.getString("xam_co"),
    Language.getString("sanh"),
    Language.getString("sanh_lon"),
    Language.getString("thung"),
    Language.getString("cu_lu"),
    Language.getString("tu_quy"),
    Language.getString("thung_pha_sanh"),
    Language.getString("thung_pha_sanh_lon")
]
MauBinhUtils.TypeThangTrang = [12, 11, 10, 9, 8, 7];
MauBinhUtils.TypeBinh = {
    TYPE_BINH_LUNG              : -1,
    TYPE_CU_LU_CHI_2            : 1,
    TYPE_SAM_CHI_DAU            : 2,
    TYPE_TU_QUI_CHI_GIUA        : 3,
    TYPE_TU_QUI_CHI_3           : 4,
    TYPE_THUNG_PHA_SANH_CHI_GIUA: 5,
    TYPE_THUNG_PHA_SANH_CHI_3   : 6,
    TYPE_TAM_SANH               : 7,
    TYPE_TAM_THUNG              : 8,
    TYPE_6_DOI                  : 9,
    TYPE_5DOI_1XAM              : 10,
    TYPE_SANH_RONG              : 11,
    TYPE_SANH_RONG_DONG_HOA     : 12,
};
MauBinhUtils.TypeCard = {
    BINH_LUNG: -1,
    MAU_THAU: 1,
    MOT_DOI: 2,
    HAI_DOI: 3,
    XAM_CO: 4,
    SANH: 5,
    SANH_LON: 6,
    THUNG: 7,
    CU_LU: 8,
    TU_QUY: 9,
    THUNG_PHA_SANH: 10,
    THUNG_PHA_SANH_LON: 11
};
MauBinhUtils.isThangTrang = function (listPlayers) {
    for (let i=0; i< listPlayers.length; i++){
        if (this.TypeThangTrang.indexOf(listPlayers[i].typeBinh) >=0){
            return  true;
        }
    }
    return  false;
}
MauBinhUtils.getTextTypeBinhResult = function (type, isSap3Chi) {
    let text = "";
    if (isSap3Chi)
        return  Language.getString("sap_ba_chi");
    type = parseInt(type);
    switch (type){
        case this.TypeBinh.TYPE_BINH_LUNG: {
            text = Language.getString("binh_lung");
            break;
        }
        case this.TypeBinh.TYPE_CU_LU_CHI_2: {
            text = Language.getString("cu_lu_chi2");
            break;
        }
        case this.TypeBinh.TYPE_SAM_CHI_DAU: {
            text = Language.getString("sam_chi_cuoi");
            break;
        }
        case this.TypeBinh.TYPE_TU_QUI_CHI_GIUA: {
            text =Language.getString("tu_quy_chi2");
            break;
        }
        case this.TypeBinh.TYPE_TU_QUI_CHI_3: {
            text = Language.getString("tu_quy_chi_dau");
            break;
        }
        case this.TypeBinh.TYPE_THUNG_PHA_SANH_CHI_GIUA: {
            text = Language.getString("thung_pha_sanh_chi2");
            break;
        }
        case this.TypeBinh.TYPE_THUNG_PHA_SANH_CHI_3: {
            text = Language.getString("thung_pha_sanh_chi_dau");
            break;
        }
    }
    return text;
}
MauBinhUtils.getCardType = function(cards){
    cards        = this.sortCards(cards);
    let typeCard = this.TypeCard.MAU_THAU;
    let newCards = [];
    if (cards.length == 3){
        for (let i = 0; i < cards.length - 1; i++) {
            if (this.getValue(cards[i]) == this.getValue(cards[i+1])){
                if(newCards.indexOf(cards[i]) == -1){
                    newCards.push(cards[i]);
                }
                newCards.push(cards[i+1]);
            }
        }
        if (newCards.length == 2)
            typeCard = this.TypeCard.MOT_DOI;
        if (newCards.length == 3)
            typeCard = this.TypeCard.XAM_CO;
        if (newCards.length == 1){
            newCards =  this.getValue(cards[0])==14? [cards[0]] : this.getValue(cards[1])==14? [cards[1]] :this.getValue(cards[2])==14? [cards[2]] : cards;
        }
        return {type: typeCard, cards: newCards};
    }else{
        for (let i = 0; i < cards.length - 1; i++) {
            if (this.getValue(cards[i]) == this.getValue(cards[i+1])){
                if(newCards.indexOf(cards[i]) == -1){
                    newCards.push(cards[i]);
                }
                newCards.push(cards[i+1]);
            }
        }
        if (newCards.length > 1){
            switch (newCards.length){
                case 2:
                    typeCard = this.TypeCard.MOT_DOI;
                    break;
                case 3:
                    typeCard = this.TypeCard.XAM_CO;
                    break;
                case 4:
                    if (this.getValue(newCards[0]) == this.getValue(newCards[3]))
                        typeCard = this.TypeCard.TU_QUY;
                    else
                        typeCard = this.TypeCard.HAI_DOI;
                    break;
                case 5:
                    typeCard = this.TypeCard.CU_LU;
                    break;
            }
            return {type: typeCard, cards: newCards};
        }else{
            for (let i = 0; i < cards.length - 1; i++) {
                if (this.getType(cards[i]) == this.getType(cards[i+1])){
                    if(newCards.indexOf(cards[i]) == -1){
                        newCards.push(cards[i]);
                    }
                    newCards.push(cards[i+1]);
                }else {
                    break;
                }
            }
            if (newCards.length == 5){
                typeCard = this.TypeCard.THUNG;
            }
            let newCardsTPS = [];
            //2,3,4,5,14
            for (let i = 0; i < cards.length - 1; i++) {
                if (this.getValue(cards[i]) == this.getValue(cards[i+1])-1){
                    if(newCardsTPS.indexOf(cards[i]) == -1){
                        newCardsTPS.push(cards[i]);
                    }
                    newCardsTPS.push(cards[i+1]);
                }else{
                    break;
                }
            }
            if (newCardsTPS.length == 4){
                if (this.getValue(newCardsTPS[0]) == 2 && this.getValue(cards[4]) == 14){
                    newCardsTPS.push(cards[3]);
                    if (typeCard != this.TypeCard.THUNG)
                        typeCard = this.TypeCard.SANH;
                    else
                        typeCard = this.TypeCard.THUNG_PHA_SANH;
                }
            }else {
                if (newCardsTPS.length == 5){
                    if (this.getValue(newCardsTPS[4]) == 14){
                        if (typeCard != this.TypeCard.THUNG)
                            typeCard = this.TypeCard.SANH_LON;
                        else
                            typeCard = this.TypeCard.THUNG_PHA_SANH_LON;
                    }else
                    if (typeCard != this.TypeCard.THUNG)
                        typeCard = this.TypeCard.SANH;
                    else
                        typeCard = this.TypeCard.THUNG_PHA_SANH;

                }else{
                    newCardsTPS = newCards.length == 5 ? newCards : [];
                }
            }

            return {type: typeCard, cards: newCardsTPS};
        }
    }
}
MauBinhUtils.isBiggerChi = function (chi1, chi2){
    if (chi1.type < chi2.type){
        return false;
    }else if (chi1.type == chi2.type){
        //Check quan
        let card1 = this.sortCards(chi1.cards);
        let card2 = this.sortCards(chi2.cards);
        switch (chi1.type){
            case this.TypeCard.MAU_THAU: {
                for (let i=card1.length-1; i>=0; i--) {
                    if (this.getValue(card1[i]) != this.getValue(card2[i])) {
                        return this.getValue(card1[i]) > this.getValue(card2[i]);
                    }
                }
                return true
            }
            case this.TypeCard.MOT_DOI: {
                return this.getValue(card1[card1.length-1]) >=this.getValue(card2[card2.length-1])
            }
            case this.TypeCard.HAI_DOI: {
                if (this.getValue(card1[card1.length-1]) != this.getValue(card2[card2.length-1])){
                    return this.getValue(card1[card1.length-1]) >=this.getValue(card2[card1.length-1]);
                }else {
                    return this.getValue(card1[0]) >=this.getValue(card2[0]);
                }
            }
            case this.TypeCard.XAM_CO: {
                return this.getValue(card1[card1.length-1]) >=this.getValue(card2[card2.length-1])
            }
            case this.TypeCard.SANH: {
                return this.getValue(card1[card1.length-1]) >=this.getValue(card2[card2.length-1])
            }
            case this.TypeCard.THUNG: {
                return this.getValue(card1[card1.length-1]) > this.getValue(card2[card2.length-1])
            }
            case this.TypeCard.CU_LU: {
                return this.getValue(card1[2]) >=this.getValue(card2[2]);
            }
            case this.TypeCard.TU_QUY: {
                return this.getValue(card1[0]) >=this.getValue(card2[0]);
            }
            case this.TypeCard.THUNG_PHA_SANH: {
                return this.getValue(card1[card1.length-1]) >=this.getValue(card2[card2.length-1])
            }
        }
    }
    return true
}
MauBinhUtils.getValue = function(b){
    if (b% 13 ==0)
        return 13;
    if (b% 13 ==1)
        return 14;
    return b % 13;
}
MauBinhUtils.getType = function(v){
    return Math.floor((v-1)  / 13);
}
MauBinhUtils.sortCards = function(cards){
    if(cards == null)
        return null;
    for(let i = 0; i < cards.length - 1; i++){
        for(let j = i+1; j < cards.length;j++){
            if(this.getValue(cards[i]) > this.getValue(cards[j])){
                let tempCard = cards[j];
                cards[j] = cards[i];
                cards[i] = tempCard;
            }
        }
    }
    return cards;
}
MauBinhUtils.getTypeChi = function(type){
    return {
        color: this.getColorFromType(type),
        text : this.TEXT_BINH_TYPE[type]
    }
}
MauBinhUtils.getColorFromType = function (type) {
    switch (type){
        case this.TypeCard.MAU_THAU: {
            return cc.Color.WHITE
        }
        case this.TypeCard.MOT_DOI: {
            return cc.Color.GREEN
        }
        case this.TypeCard.HAI_DOI: {
            return cc.Color.RED
        }
        case this.TypeCard.XAM_CO: {
            return cc.Color.GRAY
        }
        case this.TypeCard.SANH: {
            return cc.Color.BLUE
        }
        case this.TypeCard.THUNG: {
            return cc.Color.YELLOW
        }
        case this.TypeCard.CU_LU: {
            return cc.Color.ORANGE
        }
        case this.TypeCard.TU_QUY: {
            return cc.Color.CYAN
        }
        case this.TypeCard.THUNG_PHA_SANH: {
            return cc.Color.MAGENTA
        }
    }
    return cc.Color.WHITE
}

module.exports = MauBinhUtils;
